<template>
  <div class="container">
    <b-alert
    :show="dismissCountDown"
    dismissible
    :variant="mensaje.color"
    @dismissed="dismissCountDown=0"
    @dismiss-count-down="countDownChanged"
    >
      {{mensaje.texto}}
    </b-alert>
    <form @submit.prevent="editarAscensor(ascensor)" >
      <h3>Editar ascensor del contacto: {{ascensor.contacto}}</h3>
      <div class="row mt5">
        <div class="col-sm-6 col-xs-12">
          <label for="Contacto">Contacto: *</label>
          <input type="text" id="Contacto" class="form-control" placeholder="Agregar Contacto" v-model="ascensor.contacto" required>
        </div>
        <div class="col-sm-6 col-xs-12">
          <label for="Telefono">Teléfono: </label>
          <input type="number" id="Telefono" class="form-control" placeholder="Agregar telefono" v-model="ascensor.telefono">
        </div>
      </div>
      <div class="row mb5">
        <div class="col-sm-6 col-xs-12">
          <label for="Poblacion">Población: *</label>
          <input type="text" id="Poblacion" class="form-control" placeholder="Agregar Población" v-model="ascensor.poblacion" required>
        </div>
        <div class="col-sm-6 col-xs-12">
          <label for="Direccion">Dirección: *</label>
          <input type="text" id="Direccion" class="form-control" placeholder="Agregar Direccion" v-model="ascensor.direccion" required>
        </div>
      </div>
      <div class="row mb5">
        <div class="col-sm-6 col-xs-12">
          <label for="Correo">Correo: </label>
          <input type="email" id="Correo" class="form-control" placeholder="Agregar correo electronico" v-model="ascensor.correo">
        </div>
        <div class="col-sm-6 col-xs-12">
          <label for="Contrato">Contrato: *</label>
          <!-- <input type="text" id="Contrato" class="form-control" placeholder="Agregar tipo de contrato" v-model="ascensor.contrato" required> -->
          <select id="Contrato" class="form-control" v-model="ascensor.contrato">
            <option v-for="item in items" :value="item.tipo"> {{item.tipo}}</option>
          </select>
        </div>
      </div>
      <div class="row mb5">
        <div class="col-sm-6 col-xs-12">
          <label for="Situación">Situación: *</label>
          <select id="Situación" class="form-control" v-model="ascensor.situacion">
            <option v-for="item in itemsSituacion" :value="item.tipo"> {{item.tipo}}</option>
          </select>
        </div>
        <div class="col-sm-6 col-xs-12">
          <label for="Firmado">Firmado: *</label>
          <input type="text" id="Firmado" class="form-control" placeholder="¿Firmado?" v-model="ascensor.firmado" required>
        </div>
      </div>
      <div class="row mb5">
        <div class="col-sm-6 col-xs-12">
          <label for="Caducidad">Caducidad: *</label>
          <input type="text" id="Caducidad" class="form-control" placeholder="Agregar caducidad del contrato" v-model="ascensor.caducidad" required>
        </div>
        <div class="col-sm-6 col-xs-12">
          <label for="Duración">Duración: *</label>
          <input type="text" id="Duración" class="form-control" placeholder="Agregar duración del contrato" v-model="ascensor.duracion" required>
        </div>
      </div>
      <div class="row mb5">
        <div class="col-sm-3 col-xs-12">
          <label for="Precio">Precio: </label>
          <input type="number" id="Precio" class="form-control" placeholder="Importe (Solo numero)" v-model="ascensor.precio">
        </div>
      </div>
      <div class="row mb5">
        <div class="col-sm-6 col-xs-12">
          <label for="Observaciones">Observaciones y mantenimientos: </label>
          <textarea name="Observaciones" rows="10" cols="50" class="form-control" placeholder="Agregar Observaciones" v-model="ascensor.observaciones"></textarea>
        </div>
        <div class="col-sm-6 col-xs-12">
          <label for="Notas_adicionales">Notas adicionales: </label>
          <textarea name="Notas_adicionales" rows="10" cols="50" class="form-control" placeholder="Agregar Notas adicionales" v-model="ascensor.notas_adicionales"></textarea>
        </div>
      </div>
      <div class="row">
        <b-button class="btn-sm btn-block btn-success" type="submit"><font-awesome-icon icon="save"/>Guardar</b-button>
        <router-link class="btn-danger btn-sm btn-block" to="/"><font-awesome-icon icon="times"/>Cancelar</router-link>
      </div>
    </form>
  </div>
</template>

<script>
export default {
  name: 'Editar-Ascensor',
  data() {
    return {
      ascensor: {},
      mensaje: {color: 'success', texto: ''},
      dismissSecs: 2,
      dismissCountDown: 0,
      items: [
        {"tipo": "Básico"},
        {"tipo": "Semi Riesgo"},
        {"tipo": "Todo Riesgo"}],

      itemsSituacion: [
        {"tipo": "ACTIVO"},
        {"tipo": "NO ACTIVO"},],

    }
  },

  created () {
    this.axios.get(`ascensor/` + this.$route.params.id)
    .then(response => {
      this.ascensor = response.data
    })
    .catch((e) => {
      console.log(e.response);
    })
  },

  methods: {
    editarAscensor(item){
      this.axios.put(`ascensor/`+ this.$route.params.id, this.ascensor)
        .then(res => {
          this.mensaje.texto = 'Ascensor Actualizado'
          this.mensaje.color = 'success'
          this.showAlert();

          setTimeout(() => {
            this.$router.push({ name: 'Ascensores', params: { id: this.$route.params.id }})
          }, 2000);
        })
        .catch(e => {
          console.log(e);
        })
    },

    countDownChanged(dismissCountDown) {
      this.dismissCountDown = dismissCountDown
    },

    showAlert() {
      this.dismissCountDown = this.dismissSecs
    }
  },
}
</script>

