<template>
  <div class="container">
    <b-alert
    :show="dismissCountDown"
    dismissible
    :variant="mensaje.color"
    @dismissed="dismissCountDown=0"
    @dismiss-count-down="countDownChanged"
    >
      {{mensaje.texto}}
    </b-alert>
    <form @submit.prevent="agregarAscensor(ascensor)" v-if="agregar">
      <h3>Agregar nuevo ascensor</h3>
      <div class="row mt5">
        <div class="col-sm-6 col-xs-12">
          <label for="Contacto">Contacto: *</label>
          <input type="text" id="Contacto" class="form-control" placeholder="Agregar Contacto" v-model="ascensor.contacto" required>
        </div>
        <div class="col-sm-6 col-xs-12">
          <label for="Telefono">Teléfono: </label>
          <input type="number" id="Telefono" class="form-control" placeholder="Agregar telefono" v-model="ascensor.telefono">
        </div>
      </div>
      <div class="row mb5">
        <div class="col-sm-6 col-xs-12">
          <label for="Poblacion">Población: *</label>
          <input type="text" id="Poblacion" class="form-control" placeholder="Agregar Población" v-model="ascensor.poblacion" required>
        </div>
        <div class="col-sm-6 col-xs-12">
          <label for="Direccion">Dirección: *</label>
          <input type="text" id="Direccion" class="form-control" placeholder="Agregar Direccion" v-model="ascensor.direccion" required>
        </div>
      </div>
      <div class="row mb5">
        <div class="col-sm-6 col-xs-12">
          <label for="Correo">Correo:</label>
          <input type="email" id="Correo" class="form-control" placeholder="Agregar correo electronico" v-model="ascensor.correo">
        </div>
        <div class="col-sm-6 col-xs-12">
          <label for="Contrato">Contrato: *</label>
          <!-- <input type="text" id="Contrato" class="form-control" placeholder="Agregar tipo de contrato" v-model="ascensor.contrato" required> -->
          <select id="Contrato" class="form-control" v-model="ascensor.contrato">
            <option v-for="item in items" :value="item.tipo"> {{item.tipo}}</option>
          </select>
        </div>
      </div>
      <div class="row mb5">
        <div class="col-sm-6 col-xs-12">
          <label for="Situación">Situación: *</label>
          <select id="Situación" class="form-control" v-model="ascensor.situacion">
            <option v-for="item in itemsSituacion" :value="item.tipo"> {{item.tipo}}</option>
          </select>
        </div>
        <div class="col-sm-6 col-xs-12">
          <label for="Firmado">Firmado: *</label>
          <input type="text" id="Firmado" class="form-control" placeholder="¿Firmado?" v-model="ascensor.firmado" required>
        </div>
      </div>
      <div class="row mb5">
        <div class="col-sm-6 col-xs-12">
          <label for="Caducidad">Caducidad: *</label>
          <input type="text" id="Caducidad" class="form-control" placeholder="Agregar caducidad del contrato" v-model="ascensor.caducidad" required>
        </div>
        <div class="col-sm-6 col-xs-12">
          <label for="Duración">Duración: *</label>
          <input type="text" id="Duración" class="form-control" placeholder="Agregar duración del contrato" v-model="ascensor.duracion" required>
        </div>
      </div>
      <div class="row mb5">
        <div class="col-sm-3 col-xs-12">
          <label for="Precio">Precio: </label>
          <input type="number" id="Precio" class="form-control" placeholder="Importe (Solo numero)" v-model="ascensor.precio">
        </div>
      </div>
      <div class="row mb5">
        <div class="col-sm-6 col-xs-12">
          <label for="Observaciones">Observaciones y mantenimientos: </label>
          <textarea name="Observaciones" rows="10" cols="50" class="form-control" placeholder="Agregar Observaciones" v-model="ascensor.observaciones" @keydown.tab.prevent="tabber($event)"></textarea>
        </div>
        <div class="col-sm-6 col-xs-12">
          <label for="Notas_adicionales">Notas adicionales: </label>
          <textarea name="Notas_adicionales" rows="10" cols="50" class="form-control" placeholder="Agregar Notas adicionales" v-model="ascensor.notas_adicionales" @keydown.tab.prevent="tabber($event)"></textarea>
        </div>
      </div>
      <div class="row">
        <b-button class="btn-sm btn-block btn-success" type="submit"><font-awesome-icon icon="save"/>Guardar</b-button>
         <router-link class="btn-danger btn-sm btn-block" to="/"><font-awesome-icon icon="times"/>Cancelar</router-link>
      </div>
    </form>
  </div>
</template>

<script>
import { mapState } from "vuex";

export default {
  data() {
    return {
      ascensores: [],
      ascensor: {
        direccion: '',
        poblacion: '',
        contrato: 'Básico',
        situacion: 'ACTIVO',
        firmado: '',
        caducidad: '',
        duracion: '',
        precio: '',
        contacto: '',
        telefono: '',
        correo: '',
        observaciones: '',
        notas_adicionales: '',
      },
      
      items: [
        {"tipo": "Básico"},
        {"tipo": "Semi Riesgo"},
        {"tipo": "Todo Riesgo"}],

      itemsSituacion: [
        {"tipo": "ACTIVO"},
        {"tipo": "NO ACTIVO"},],

      agregar: true,

      mensaje: {color: 'success', texto: ''},
      dismissSecs: 2,
      dismissCountDown: 0
    }
  },

  computed: {
    ...mapState(['token'])
  },

  methods: {
    agregarAscensor(item) {
      let config = {
        headers: {
          token: this.token
        }
      }

      this.axios.post('/new-ascensor', item, config)
      .then(res => {
        this.ascensores.push(res.data);
        
        this.ascensor.direccion = '';
        this.ascensor.poblacion = '';
        this.ascensor.contrato = '';
        this.ascensor.situacion = '';
        this.ascensor.firmado = '';
        this.ascensor.caducidad = '';
        this.ascensor.duracion = '';
        this.ascensor.precio = '';
        this.ascensor.contacto = '';
        this.ascensor.telefono = '';
        this.ascensor.correo = '';
        this.ascensor.observaciones = '';
        this.ascensor.notas_adicionales = '';

        //Alert message
        this.mensaje.texto = 'Ascensor Agregado!'
        this.mensaje.color = 'success'
        this.showAlert();

      })
      .catch((e) => {
        //Alert message
        this.mensaje.texto = 'Algún dato no se ha introducido correctamente'
        this.mensaje.color = 'danger';
        this.showAlert();
      })
    },

    tabber (event) {
    let text = this.chapter.text,
      originalSelectionStart = event.target.selectionStart,
      textStart = text.slice(0, originalSelectionStart),
      textEnd =  text.slice(originalSelectionStart);

      this.chapter.text = `${textStart}\t${textEnd}`
      event.target.value = this.chapter.text // required to make the cursor stay in place.
      event.target.selectionEnd = event.target.selectionStart = originalSelectionStart + 1
    },

    countDownChanged(dismissCountDown) {
      this.dismissCountDown = dismissCountDown
    },

    showAlert() {
      this.dismissCountDown = this.dismissSecs
    }
  },
}
</script>

<style scoped>

  label {
    display: block;
    text-align: left;
    margin-top: 10px;
  }

  input, textarea {
    width: 100%;
  }

  .row {
    margin-top: 20px;
  }
  
  button, a {
    font-size: 20px;
    margin-top: 20px;
    padding: 10px;
  }

  a:hover {
    text-decoration: none;
  }

  svg {
    margin-right: 10px
  }

  input:focus, textarea:focus, select:focus {
    border-color: #b31f23;
    box-shadow: 0 0 0 0.2rem #b31f23;
    transition: all 1s;
  }
</style>
