<template>
  <div class="container">
    <div class="form">
    <b-alert
      :show="dismissCountDown"
      dismissible
      :variant="mensaje.color"
      @dismissed="dismissCountDown=0"
      @dismiss-count-down="countDownChanged"
      >
        {{mensaje.texto}}
      </b-alert>
      <form @submit.prevent="login">
        <label for="usuario">Usuario:</label>
        <input type="text" id="usuario" class="form-control" placeholder="email" v-model="usuario.email">
        <label for="password">Contraseña:</label>
        <input type="password" id="password" class="form-control my-2" placeholder="Password" v-model="usuario.pass">

        <b-button type="submit" class="btn-sm btn-block btn-success">Acceder</b-button>
      </form>
    </div>
  </div>
</template>

<script>

import { mapState, mapMutations, mapActions } from 'vuex';

export default {
  data() {
    return {
      usuario: { email: '', pass: ''},
      mensaje: {color: 'danger', texto: ''},
      dismissSecs: 4,
      dismissCountDown: 0
    }
  },

  methods: {
    ...mapActions(['guardarUsuario']),

    login() {
      this.axios.post('/login', this.usuario)
          .then( res => {
            const token = res.data.token;

            this.guardarUsuario(token);
          })
          .catch(err => {
            this.mensaje.texto = 'Usuario no valido';
            this.showAlert();
          })
    },

    countDownChanged(dismissCountDown) {
      this.dismissCountDown = dismissCountDown
    },

    showAlert() {
      this.dismissCountDown = this.dismissSecs
    }
  },
}
</script>
  
<style>
  .form {
    background: rgba(0,0,0,.03);
    margin: auto;
    margin-top: 100px;
    max-width: 475px;
    padding: 20px;
  }

  .form form label {
    text-align: left;
    width: 100%;
  }

  .form form label:hover {
    cursor: pointer;
  }

  .form form input {
    margin: 10px 0;
    height: 50px;
  }

  .form form button {
    font-weight: bold;
    font-size: 20px;
  }

  .form .alert {
    margin-top: 20px;
  }
</style>
