<template>
  <div class="container">
    <b-alert
      :show="dismissCountDown"
      dismissible
      :variant="mensaje.color"
      @dismissed="dismissCountDown=0"
      @dismiss-count-down="countDownChanged"
    >
      {{mensaje.texto}}
    </b-alert>
    <router-link class="btn-success btn-sm btn-block agregar" to="nuevo-ascensor"><font-awesome-icon icon="plus"/>Agregar nuevo ascensor</router-link>

    <div class="row">
      <div class="col-lg-12 busqueda">
        <input type="text" placeholder="Buscar por...." class="form-control col-lg-9 col-xs-9 col-xs-12 mt-2 mb-2" v-model="texto">
        <select v-model= "value" class="form-control col-lg-3 col-xs-3 mt-2 mb-2">
          <option value="direccion">direccion</option>
          <option value="contacto">contacto</option>
        </select>
      </div>
    </div>
    <div class="row mt-4 mb-2">
      <div class="col-lg-12 ascensores-enlace">
        <div class="row">
          <div class="col-lg-6 col-xs-12">
            <h4>Total ascensores Activos: <span>{{totalActivos}}</span></h4>
          </div>

          <div class="col-lg-6 col-xs-12">
            <h4>Total ascensores No Activos: <span>{{totalNoActivos}}</span></h4>
          </div>
        </div>
        <div class="row mt-3">
          <div class="col-lg-12 col-xs-12">
            <h4>Coste total de los mantenimientos: <span>{{total}}€</span></h4>
          </div>
        </div>
      </div>
    </div>
    <div class="card-columns col-xs-12">
      <div class="card" v-for="item of ascensoresFiltrados" :key="item._id">
        <div class="card-body">
          <h4 class="card-title">{{item.contacto}}</h4>
          <p class="card-text"><font-awesome-icon icon="home"/>{{item.direccion}}</p>
          <div class="dates_contact col-lg-6">
            <small class="card-text text-left"><font-awesome-icon icon="phone"/>{{item.telefono}}</small>
          </div>
          <div class="dates_contact col-lg-6">
            <small class="card-text text-right"><font-awesome-icon icon="city"/>{{item.poblacion}}</small>
          </div>
        </div>
        <div class="dates_contact col-lg-12" v-if="item.situacion == 'ACTIVO'">
         <span>Situacion:<small class="card-text text-right text-success font-weight-bold"> {{item.situacion}}</small></span>
        </div>
        <div class="dates_contact col-lg-12" v-if="item.situacion == 'NO ACTIVO'">
         <span>Situacion:<small class="card-text text-right text-danger font-weight-bold"> {{item.situacion}}</small></span>
        </div>
        <div class="card-footer">
          <small class="text-muted">{{item.date | formatDate}}</small>
          <div class="row">
            <router-link :to="{name: 'Ver Ascensor', params: {id: item._id}}">
              <b-button class="bg-info btn-sm mx-2"><font-awesome-icon icon="eye"/>Ver</b-button>
            </router-link>
            <router-link :to="{name: 'Editar Ascensor', params: {id: item._id}}">
              <b-button class="bg-primary btn-sm mx-2"><font-awesome-icon icon="edit"/>Actualizar</b-button>
            </router-link>
            <b-button class="bg-danger btn-sm mx-2" @click="eliminarAscensor(item._id)"><font-awesome-icon icon="trash"/>Eliminar</b-button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
export default {

  data() {
    return {
      texto: '',
      textoContacto: '',
      value: 'direccion',
      ascensores: [],
      precio: '',

      mensaje: {color: 'success', texto: ''},
      dismissSecs: 2,
      dismissCountDown: 0,
    };
  },

  // computed: {
  //   ...mapState(['token'])
  // },

  created() {
    this.listaAscensores();
  },

  computed: {
    ...mapState(['token']),

    ascensoresFiltrados(){
      function compare(a, b) {
        if (a.name < b.name) return -1;
        if (a.name > b.name) return 1;
        return 0;
      }

      if (this.value == 'direccion') {
        return this.ascensores.filter(ascensor => {
          return ascensor.direccion.toLowerCase().includes(this.texto.toLowerCase())
        }).sort(compare)  
      } else {
        return this.ascensores.filter(ascensor => {
          return ascensor.contacto.toLowerCase().includes(this.texto.toLowerCase())
        }).sort(compare) 
      }
    },

    total: function() {
      let sum = 0;
      this.ascensoresFiltrados.forEach(function(item) {
        if (item.situacion == 'ACTIVO') {
          sum = sum + item.precio
        }
      });

      return sum
    },

    totalActivos: function() {
      let total = 0;
      this.ascensoresFiltrados.forEach(function(item) {
        if (item.situacion == 'ACTIVO') {
          total = total + 1;
        }
      });

      return total
    },

    totalNoActivos: function() {
      let total = 0;
      this.ascensoresFiltrados.forEach(function(item) {
        if (item.situacion == 'NO ACTIVO') {
          total = total + 1;
        }
      });

      return total
    }
  },

  methods: {

    listaAscensores() {
      let config = {
        headers: {
          token: this.token
        }
      }

      this.axios.get('/ascensor', config)
      .then((response) => {
        this.ascensores = response.data
      })
      .catch((e) => {
       console.log('error' + e)
      })
    },

    eliminarAscensor(id) {
      if (confirm('¿Desea borrar el ascensor?')) {
        this.axios.delete(`ascensor/${id}`)
        .then(res => {
            let index = this.ascensores.findIndex( item => item._id === res.data._id )
            this.ascensores.splice(index, 1);
            
            this.showAlert();
            this.mensaje.texto = 'Ascensor Eliminado!'
            this.mensaje.color = 'danger'
        })
        .catch((e) => {
          console.log(e.response);
        })
      }
    },

    countDownChanged(dismissCountDown) {
      this.dismissCountDown = dismissCountDown
    },
    showAlert() {
      this.dismissCountDown = this.dismissSecs
    }
  },
}
</script>
